.Aboutjourney{
    margin-top: 60px;
}

.Aboutjourney h1{
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    color: #242524ff;
}

.Aboutjourney .cards{
    margin-top: 45px;
    padding: 0 40px;
    display: grid; /* Use CSS Grid */
    grid-template-columns: repeat(3, 1fr); /* Three items per row */
    gap: 10px; /* Space between articles */
    justify-items: center;
}

.Aboutjourney .cards .card{
    width: 400px;
    height: 210px;
    border-radius: 8px;
    border-color: #ebebeaff;
    border-width: 1px;
    border-style: solid;
    box-shadow: 0px 0px 1px #171a1f12 0px 0px 2px #171a1f1f;
    padding: 10px 20px;
    
}

.Aboutjourney .cards .card small{
    font-size: 11px;
    color: #e8618cff;
    font-weight: 600;
    line-height: 18px;
}

.Aboutjourney .cards .card h3{
    font-size: 20px;
    font-weight: 600;
    color: #242524ff;
    margin-top: 5px;
}

.Aboutjourney .cards .card p{
    font-size: 12px;
    line-height: 20px;
    color: #8c8d8bff;
    font-weight: 400;
    margin-top: 30px;
}

@media (max-width: 950px) {
    .Aboutjourney .cards{
         grid-template-columns: repeat(2, 1fr);
    }

    .Aboutjourney .cards .card{
    width: 300px;
    height: auto;
    border-radius: 8px;
    border-color: #ebebeaff;
    border-width: 1px;
    gap: 20px;
    border-style: solid;
    box-shadow: 0px 0px 1px #171a1f12 0px 0px 2px #171a1f1f;
    padding: 10px 20px;
    
}

.Aboutjourney h1{
    font-size: 1.2em;
    text-align: center;
}
}

@media (max-width: 600px) {
     .Aboutjourney .cards{
         grid-template-columns: 1fr;
    }
}
