.Blogleft{
    margin-top: 70px;
    width: 70%;
}

.Blogleft .main-img{
    width: 800px;
    height: 280px;
     overflow: hidden;
     margin-bottom: 20px;
}

.Blogleft .main-img img{
    width: 100%;
    height: 100%;
    border-radius: 8px;
     box-shadow: 0px 0px 1px #171a1f12, 0px 0px 2px #171a1f1f;
    object-fit: cover;
}

.Blogleft small{
    margin-top: 40px;
    margin-left: 20px;
    font-size: 11px;
    font-weight: 400;
    color: #242524ff;
}

.Blogleft h1{
    margin-top: 30px;
    margin-left: 20px;
    color: #242524ff;
    font-weight: 800;
    font-size: 40px;
}

.Blogleft .cap-det{
    display: flex;
    margin-left: 20px;
    font-size: 11px;
    font-weight: 600;
    color: #8c8d8bff;
    margin-top: 15px;
}

.Blogleft .cap-det .oct{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: 20px;
    margin-top: 10px;
}

.Blogleft .cap-det .first{
    margin-top: 9px;
}

.Blogleft .cap-det .first span{
    color: #636ae8ff;
}

.Blogleft .cap-det .oct p{
    margin-left: 5px;
}

 .Blogleft .cap-det .cap-det-ryt{
    display: flex;
    align-items: flex-end;
    /* margin-top: 8px; */
    margin-left: 30px;
} 

.Blogleft .cap-det .cap-det-ryt p{
    font-size: 9px;
    line-height: 20px;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 9999px;
    border: 1px solid #ebebeaff;
    padding-left: 5px;
    padding-right: 5px;
    margin-left: 10px;
}

.Blogleft .para{
    font-size: 14px;
    line-height: 22px;
    font-weight: 400;
    color: #242524ff;
    justify-content: flex-start;
    margin-left: 20px;
    margin-top: 30px;
}

 /* Responsive Styles */
@media (max-width: 1200px) {
    .Blogleft {
        width: 90%; /* Adjust width for smaller screens */
        margin: 20px auto; /* Centering the blog left section */
    }

    .Blogleft .main-img {
        width: 100%; /* Full width for images */
        height: auto; /* Maintain aspect ratio */
    }

    .Blogleft h1 {
        font-size: 32px; /* Reduce font size for headings */
        margin-left: 10px; /* Adjust margin */
    }

    .Blogleft small {
        margin-left: 10px; /* Adjust margin */
    }

    .Blogleft .cap-det {
        flex-direction: column; /* Stack items vertically */
        margin-left: 10px; /* Adjust margin */
    }

    .Blogleft .cap-det .cap-det-ryt {
        margin-left: 0; /* Remove left margin */
        margin-top: 5px; /* Add some top margin */
    }

    .Blogleft .cap-det .cap-det-ryt p {
        font-size: 8px; /* Reduce font size */
        padding: 3px; /* Adjust padding */
    }

    .Blogleft .para {
        font-size: 12px; /* Reduce font size */
        margin-left: 10px; /* Adjust margin */
    }
}

@media (max-width: 768px) {
    .Blogleft {
        margin-top: 40px; /* Reduce top margin */
    }

    .Blogleft h1 {
        font-size: 28px; /* Further reduce font size */
    }

    .Blogleft .main-img {
        height: 200px; /* Adjust height */
    }

    .Blogleft .para {
        font-size: 11px; /* Further reduce font size */
    }
}

@media (max-width: 480px) {
    .Blogleft h1 {
        font-size: 24px; /* Further reduce for small screens */
    }

    .Blogleft .main-img {
        height: auto; /* Allow images to resize */
    }

    .Blogleft small {
        font-size: 10px; /* Further reduce font size */
    }

    .Blogleft .cap-det {
        flex-direction: column; /* Stack items */
    }

    .Blogleft .cap-det .cap-det-ryt p {
        font-size: 7px; /* Further reduce font size */
    }

    .Blogleft .para {
        font-size: 10px; /* Further reduce font size */
    }
}