.Aboutprinc{
    margin-top: 100px;
}

.Aboutprinc h1{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 40px;
    font-weight: 700;
    color: #242524ff;
}

.Aboutprinc .cards{
    margin-top: 30px;
    padding: 0 40px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
}

.Aboutprinc .cards .card{
    width: 395px;
    height: 210px;
    padding: 10px 20px;
    border-color: #ebebeaff;
    border-width: 1px;
    border-style: solid;
    border-radius: 8px;
    box-shadow: 0px 0px 1px #171a1f12, 0px 0px 2px #171a1f1f;
}

.Aboutprinc .cards .card img{
   margin-left: 44.6%;
   margin-top: 20px;
   
}

.Aboutprinc .cards .card h3{
    font-size: 18px;
    font-weight: 600;
    color: #242524ff;
   text-align: center;
}

.Aboutprinc .cards .card p{
   text-align: center;
   margin-top: 8px;
   font-size: 11px;
   color: #8c8d8bff;
   font-weight: 400;
   line-height: 20px;
   /* width: 320px; */
}

.com{
    width: 50px;
    
}

.em{
    width: 50px;
}

@media (max-width: 950px) {
    .Aboutprinc .cards{
        grid-template-columns: repeat(2, 1fr);
        
    }

    .Aboutprinc h1{
        font-size: 1.2em;
    text-align: center;
    }
    

    .Aboutprinc .cards .card{
        width: 300px;
        height: 280px;
    }
}

@media (max-width: 600px) {
    .Aboutprinc .cards{
        grid-template-columns: 1fr;
    }
}