.Blogright{
    margin-top: 80px;
    padding: 20px;
}

.Blogright .cards .card{
    width: 350px;
    height: 210px;
    border-radius: 8px;
    border:  1px solid #ebebeaff;
    box-shadow: 0px 0px 1px #171a1f12, 0px 0px 2px #171a1f1f;
    overflow: hidden;
    margin-bottom: 20px;
    
}

.Blogright .cards{
    margin-top: 20px;
    
}

.Blogright .card img{
    width: 400px;
    height: 140px;
    border-radius: 0px 0px 0px 0px;
    object-fit: cover;
    margin-bottom: 10px;
}

.Blogright .card p{
    font-size: 14px;
    line-height: 23px;
    font-weight: 600;
    color: #242542ff;
    margin-left: 10px;
}

.Blogright .card small{
    font-size: 11px;
    color: #8c8d8bff;
    font-weight: 400;
    margin-left: 10px;
    line-height: 20px;
}

@media (max-width: 1250px) {
    
    .Blogright .cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
        padding: 20px;
    }

    .Blogright .card{
        width: 250px;
        height: 150px;
    }
}

@media (max-width: 1024px) {
    .Blogright .cards{
        grid-template-columns: repeat(3, 1fr);
    }

    .Blogright .cards .card{
        width: 250px;
        height: 250px;
    }
}

@media (max-width: 950px) {
    .Blogright .cards{
        grid-template-columns: repeat(2, 1fr);
    }
    
}

@media (max-width: 600px) {
    .Blogright .cards{
        grid-template-columns: 1fr;
    }

    .Blogright .cards .card{
        width: 300px;
        height: 210px;
    }

    .Blogright{
        padding: 20px;
    }
}

