.Contactmess{
    margin-top: 60px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.Contactmess .cont-left{
    /* padding: 0 20px; */
    width: 40%;
    height: 530px;
}

.Contactmess h3{
    font-size: 25px;
    line-height: 30px;
    color: #242524ff;
    margin-bottom: 30px;
}

.Contactmess .cont-left p{
    font-size: 10px;
    font-weight: 500;
    line-height: 14px;
    color: #242524ff;
    margin-bottom: 5px;
}

.Contactmess .cont-left input{
    width: 550px;
    height: 35px;
    font-size: 11px;
    font-weight: 400;
    border-radius: 6px;
    border: 1px solid #ebebeaff;
    outline: none;
    padding-left: 12px;
    padding-right: 12px;
    margin-bottom: 30px;
    line-height: 22px;

}

.Contactmess .cont-left input:hover{
    color: #a7a9a7ff;
}

.cont-left .message textarea{
    width: 550px;
    height: 100px;
    padding-bottom: 7px;
    padding-top: 7px;
    padding-left: 12px;
    padding-right: 12px;
    border-radius: 6px;
    border: 1px solid #ebebeaff;
    color: #a7a9a7ff;
    font-weight: 400;
    font-size: 11px;
}

.cont-left button{
    width: 550px;
    height: 35px;
    padding: 0 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 11px;
    line-height: 22px;
    color: #fff;
    background: #636ae8ff;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    margin-top: 40px;
}

.cont-left button:hover{
    color: #fff;
    background: #4850e4ff;
}

.cont-right{
   width: 580px;
   height: 450px;
   border-radius: 10px;
   overflow: hidden;
}

.cont-right img{
    width: 100%;
    height: 80%;
    object-fit: cover;
    border-radius: 10px;
}

/* Additional Styles for Responsiveness */

@media (max-width: 1200px) {
    .Contactmess {
        flex-direction: column; /* Stack left and right sections */
        align-items: center; /* Center items */
    }

    .Contactmess .cont-left,
    .Contactmess .cont-right {
        width: 80%; /* Adjust width for smaller screens */
    }

    .Contactmess .cont-left input,
    .cont-left .message textarea,
    .cont-left button {
        width: 100%; /* Full width for input elements */
    }
}

@media (max-width: 800px) {
    .Contactmess h3 {
        font-size: 22px; /* Reduce heading size */
    }

    .Contactmess .cont-left p {
        font-size: 9px; /* Reduce paragraph size */
    }
}

@media (max-width: 500px) {
    .Contactmess {
        margin-top: 30px; /* Adjust margin for smaller screens */
    }

    .Contactmess h3 {
        font-size: 20px; /* Further reduce heading size */
    }

    .Contactmess .cont-left input,
    .cont-left .message textarea,
    .cont-left button {
        height: 30px; /* Reduce height for input elements */
        font-size: 10px; /* Adjust font size */
    }
}
