.Abouthead{
    margin-top: 30px;
     position: relative;
    overflow: hidden;
    text-align: center;
}

.Abouthead .abt-img img{
     width: 100%;
    height: auto;
}

.Abouthead .overlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #ffffffb3;
    z-index: 1; 
}

.Abouthead .writing{
    bottom: 15%;
    left: 20%;
    position: absolute; 
    z-index: 2;   
    padding: 20px; 
    display: flex; 
    width: 60%;
    flex-direction: column; 
    align-items: center; 
    justify-content: center; 
    height: 100%;
}

.Abouthead .writing h1{
    font-size: 3.5em; 
    font-weight: 800;
    color: #242524ff;
}

.Abouthead .writing p{
     font-size: 1.2em; 
    font-weight: 400;
    color: #8c8d8bff;
    margin-top: 20px;
    line-height: 28px;
    width: 80%;
}

.Abouthead .writing button{
    background-color: #636ae8ff; /* White background for explore button */
    color: #fff; 
    font-weight: 500;
    margin-top: 20px;
}

.Abouthead .writing button:hover{
    color: #ffffffff;
    background: #2c35e0ff;
}

@media (max-width: 950px) {
    .Abouthead .writing h1{
        font-size: 1.2em;
        opacity: 1;
       
    }

    .Abouthead .writing p{
        font-size: 0.8em;
        opacity: 0.8;
    }

    .Abouthead .writing{
        width: 90%;
        top: 10%;
        right: 5%;
        left: 5%;
    }

}

@media (max-width: 600px) {
     .Abouthead .writing h1{
        font-size: 1.2em;
    }

     .Abouthead .writing p{
        font-size: 0.7em;
       line-height: 15px;
    }

    .Abouthead .writing{
        padding: 10px;
    }

}