.Post{
    margin-top: 80px;
    padding: 40px;
    background-color: #f0f0f0;
}

.Post h3{
    font-size: 20px;
    font-weight: 700;
    color: #242524ff;
    line-height: 30px;
}

.Post .img{
    padding: 0 12px;
    width: 190px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 11px;
    line-height: 22px;
    font-weight: 500;
    color: #636ae8ff;
    background: #fff;
    border: 1px solid #636ae8ff;
    border-radius: 6px;
    margin-top: 20px;
    cursor: pointer;
}

.Post .img:hover{
    color: #fff;
}

.Post input{
    border: none;
    width: 220px;
    height: 45px;
    font-size: 25px;
    font-weight: 600;
    color: #8c8d8bff;
    background: #f0f0f0;;
    margin-top: 10px;
    line-height: 40px;
    outline: none;
    padding: 0 12px;
}


.Post .pick label{
    font-size: 14px;
    font-weight: 600;
    line-height: 22px;
    color: #8c8d8bff;
    margin-top: 10px;
    margin-right: 10px;
}

.Post .pick select{
    outline: none;
    width: 150px;
    height: 30px;
    border-radius: 6px;
    font-size: 13px;
    font-weight: 400;
    padding: 0 12px;
    border: none;
    box-shadow: 0px 0px 1px #171a1f12, 0px 0px 2px #171a1f1f;
}

.Post textarea{
    width: 400px;
    height: 40px;
    margin-top: 20px;
    outline: none;
    font-size: 13px;
    line-height: 22px;
    padding: 5px 12px;
    border-radius: 6px;
    border: none;
    box-shadow: 0px 0px 2px #ccc, 0px 0px 2px #171a1f1f;
}

.Post textarea:hover{
    box-shadow: 0px 0px 2px #000, 0px 0px 2px #242524ff;
}

.Post .big-field{
    width: 1200px;
    min-height: 300px;
    border-radius: 20px;
    margin-top: 25px;
    background: #fff;
}

.Post .ql-container{
    min-height: 300px;
    background: #fff;
}

.Post button{
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    font-weight: 500;
    color: #fff;
    border: none;
    line-height: 22px;
    padding: 0 12px;
    width: 90px;
    height: 35px;
    border-radius: 6px;
    cursor: pointer;
    background: #636ae8ff;
}

.Post button:hover{
    background: #4850e4ff;
}

@media (max-width: 768px) {
    .Post {
        padding: 20px; /* Reduced padding */
    }

    .Post input,
    .Post .big-field,
    .Post textarea {
        width: 100%; /* Full width on smaller screens */
    }

    .Post .img {
        width: 100%; /* Full width for the button */
    }

    .Post .pick select {
        width: 100%; /* Full width for the select */
    }

    .Post button {
        width: 100%; /* Full width for the button */
    }
}

@media (max-width: 480px) {
    .Post h3 {
        font-size: 18px; /* Smaller heading on very small screens */
    }

    .Post input,
    .Post .big-field,
    .Post textarea {
        font-size: 18px; /* Adjust font size */
    }

    .Post .pick label {
        font-size: 12px; /* Smaller label */
    }

    .Post .pick select {
        font-size: 12px; /* Smaller select font */
    }
}