.Contacthead{
    margin-top: 40px;
}

.Contacthead .conta-top{
    width: 100%;
    height: 290px;
    background: linear-gradient(103.25deg, #ffffffff 0%, #E8618cff 90%, #e8618c0d 100%);
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px 20px;
}

.conta-top h1{
    font-weight: 800;
    font-size: 50px;
    line-height: 50px;
    color: #000;
    
}

.conta-top p{
    font-size: 17px;
    color: #8c8d8bff;
    font-weight: 400;
    margin-top: 20px;
}

.Contacthead .details{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    border-radius: 8px;
    margin-top: 25px;
    border: 1px solid #ebebeaff;
    box-shadow: 0px 0px 1px #171a1f12, 0px 0px 1px #171a1f1f;
}

.Contacthead .details .item{
    display: flex;
    justify-content: flex-start;
    align-items: center;

}

.Contacthead .details .item img{
    width: 70px;
    height: 70px;
    object-fit: cover;
    border-radius: 50%;
    padding: 2px;
    display: flex;
    align-items: center;
}

.details .item small{
    font-size: 10px;
    line-height: 18px;
    font-weight: 500;
    color: #8c8d8bff;
}

.details .item p{
    font-size: 12px;
    color: #242524ff;
    line-height: 23px;

}

/* Additional Styles for Responsiveness */

@media (max-width: 1200px) {
    .Contacthead .conta-top {
        padding: 15px;
    }
}

@media (max-width: 900px) {
    .Contacthead .details {
        gap: 15px;
    }
}

@media (max-width: 750px) {
    .Contacthead .details .item {
        flex-direction: column; /* Stack items vertically */
        align-items: center; /* Center items */
        text-align: center; /* Center text */
    }

    .Contacthead .details .item img {
        margin-bottom: 10px; /* Space between image and text */
    }
}

@media (max-width: 500px) {
    .Contacthead .conta-top h1 {
        font-size: 1.2em; /* Further reduce font size */
    }

    .Contacthead .conta-top p {
        font-size: 0.7em; /* Further reduce font size */
    }

    .Contacthead .details .item p {
        font-size: 11px; /* Adjust font size for smaller screens */
    }
}
