.Aboutcomm{
    margin-top: 80px;

}

.Aboutcomm h1{
    text-align: center;
    font-size: 40px;
    font-weight: 700;
    color: #242524ff;
}

.Aboutcomm .cards{
    margin-top: 20px;
    padding: 10px 40px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
}

.Aboutcomm .card{
    width: 600px;
    height: 190px;
    border-radius: 12px;
    border: 1px solid #ebebeaff;
    box-shadow: 0px 0px 1px #171a1f12, 0px 0px 2px #171a1f1f;
    display: flex;
    justify-content: space-between;
    padding: 20px;
}

.Aboutcomm .card img{
    width: 80px;
    height: 80px;
    border: 1px solid #ebebeaff;
    border-radius: 50%;
}

.Aboutcomm .card .testimony .test{
    font-size: 14px;
    font-weight: 400;
    color: #242524ff;
    line-height: 25px;
    margin-left: 30px;
}

.Aboutcomm .card .testimony .test-det{
    margin-top: 10px;
}

.Aboutcomm .card .test-det small{
    font-size: 12px;
    color: #636ae8ff;
    font-weight: 600;
    line-height: 20px;

}

.Aboutcomm .test-det p{
    font-size: 10px;
    font-weight: 400;
    line-height: 18px;
    color: #8c8d8bff;
}

@media (max-width: 950px) {

    .Aboutcomm h1{
        font-size: 1.2em;
        text-align: center;
    }

    .Aboutcomm .cards{
        grid-template-columns: repeat(2, 1fr);
    }

    .Aboutcomm .card{
        width: 350px;
        height: 250px;
    }
}

@media (max-width: 600px) {
    .Aboutcomm .card{
        width: 300px;
        height: 350px;
    }

     .Aboutcomm .cards{
        grid-template-columns: 1fr;
    }
}