.Comment{
    padding: 20px;
    margin-top: 10px;
}

.Comment h1{
    font-size: 30px;
    font-weight: 700;
    color: #242524ff;
}

.comment-area{
    margin-top: 20px;
    height: 295px;
    width: 1250px;
    padding: 20px;
    border-radius: 8px;
    border: 1px solid #ebebeaff;
    box-shadow: 0px 0px 1px #171a1f12, 0px 0px 2px #171a1f1f;
}

.comment-area p{
    font-size: 16px;
    line-height: 26px;
    color: #242524ff;
    font-weight: 600;
}

.comment-area textarea{
    width: 1200px;
    height: 98px;
    padding-top: 7px;
    padding-bottom: 7px;
    padding-left: 12px;
    padding-right: 12px;
    font-size: 14px;
    line-height: 22px;
    font-weight: 400;
    margin-top: 10px;
    color: #a7a9a7ff;
    border-radius: 6px;
    border: 1px solid #ebebeaff;
}

.comment-area input{
    margin-top: 10px;
    width: 1200px;
    padding-left: 12px;
    padding-right: 12px;
    font-size: 14px;
    line-height: 22px;
    font-weight: 400;
    border-radius: 6px;
    height: 40px;
    border: 1px solid #ebebeaff;
    outline: none;
}

.comment-area button{
    width: 125px;
    height: 40px;
    padding: 0 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    line-height: 22px;
    font-weight: 500;
    background: #636ae8ff;
    border: none;
    cursor: pointer;
    margin-top: 20px;
    border-radius: 6px;
    color: #fff;
}

.comment-area button:hover{
    color: #fff;
    background: #4850e4ff;
}

.comments{
    margin-top: 10px;
}

.comment{
    display: flex;
    gap: 10px;
    padding: 20px;
    border-bottom: 1px solid #ebebeaff;
}

.comment img{
    width: 50px;
    height: 40px;
    overflow: hidden;
    object-fit: cover;
    border-radius: 50%;
    border: 1px solid #ccc;
}

.comment h3{
    font-size: 14px;
    font-weight: 600;
    line-height: 12px;
    color: #242524ff;
}
.comment small{
    font-size: 10px;
    color: #8c8d8bff;
    font-weight: 400;
    line-height: 16px;

}

.comment p{
    font-size: 13px;
    font-weight: 400;
    color: #8c8d8bff;
    line-height: 22px;

}

@media (max-width: 1200px) {
     .comment-area {
    margin-top: 20px;
    height: auto;
    width: 800px;
    padding: 20px;
    border-radius: 8px;
    border: 1px solid #ebebeaff;
    box-shadow: 0px 0px 1px #171a1f12, 0px 0px 2px #171a1f1f;
    }

    .comment-area textarea{
        width: 700px;
        height: 98px;
    }

    .comment-area input{
        width: 700px;

    }

    .comment {
        width: 800px;
    }
}



@media (max-width: 950px) {
    .comment-area {
    margin-top: 20px;
    height: auto;
    width: 600px;
    padding: 20px 20px;
    border-radius: 8px;
    border: 1px solid #ebebeaff;
    box-shadow: 0px 0px 1px #171a1f12, 0px 0px 2px #171a1f1f;
    }

    .comment-area h1 {
        font-size: 18px; /* Reduce font size for smaller screens */
    }

    .comment-area textarea{
         width: 500px;
    height: 98px;
    font-size: 12px;
    line-height: 22px;
    font-weight: 400;
    margin-top: 10px;
    color: #a7a9a7ff;
    border-radius: 6px;
    border: 1px solid #ebebeaff;
    }

    .comment-area input{
        font-size: 12px;
        width: 500px;
    }

    .comment-area p {
        font-size: 14px; /* Reduce font size */
    }


    .comment-area button {
        width: 60%; /* Full width button */
    }

    .comment {
        flex-direction: column; /* Stack comments vertically */
        align-items: flex-start; /* Align items to the start */
        width: 700px;
    }

    .comment img {
        width: 40px; /* Reduce image size */
        height: 30px;
    }

    .comment h3 {
        font-size: 12px; /* Reduce font size */
    }

    .comment small {
        font-size: 9px; /* Reduce font size */
    }

    .comment p {
        font-size: 12px; /* Reduce font size */
    }
}

@media (max-width: 680px) {
    .comment-area {
        padding: 15px;
        width: 300px;
        height: 270px;
    }

    .comment-area textarea,
    .comment-area input {
        
    width: 250px;
    
    font-size: 12px;
    line-height: 22px;
    font-weight: 400;
    margin-top: 10px;
    color: #a7a9a7ff;
    border-radius: 6px;
    border: 1px solid #ebebeaff;
    }

    .comment-area button {
        height: 30px; /* Adjust button height */
        font-size: 0.8rem;
    }

    .comment{
        width: 350px;
    }
}