.Aboutread{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
}

.Aboutread .ready{
    background: #E8618cff;
    display: flex;                
    flex-direction: column;    
    justify-content: center;      
    align-items: center;          
    text-align: center;           
    padding: 10px 10px;    
    width: 1300px; /* Use percentage for responsiveness */
    max-width: 1300px; /* Max width for larger screens */
    height: 330px; /* Auto height for flexibility */
    border-radius: 8px; 
}

.Aboutread .ready h1{
    width: 100%; /* Full width */
    font-weight: 700;
    font-size: 40px;
    color: #fff;

}

.Aboutread .ready p{
     margin-top: 10px;
    width: 100%; /* Full width */
    font-size: 15px; /* Responsive font size */
    line-height: 22px; /* Adjust line height for readability */
    font-weight: 400;
    color: #ffffffe6;
}

.Aboutread .ready button{
     width: 180px; /* Use percentage for responsiveness */
    margin: 20px 0; /* Vertical margin for spacing */
    font-size: 11px; /* Responsive font size */
    font-weight: 500;
    cursor: pointer;
    color: #323842ff;
    background: #fFF;
    border: 1px solid white;
    border-radius: 6px;
    padding: 5px 12px;
}

.Aboutread .ready button:hover{
    color: #181b21ff;
    background: #ffffffe6;
}

@media (max-width: 950px) {
    .ready h1{
        font-size: 1.5em;
    }

    .ready p{
        font-size: 0.9em;
    }

    .ready button{
        font-size: 0.9em;
    }
}

@media (max-width: 600px) {
    .ready h1{
        font-size: 1.3em;
    }

    .ready p{
         font-size: 0.8em;
    }

    .ready button{
         font-size: 0.8em;
    }
}