.Aboutteam{
    margin-top: 80px;
}

.Aboutteam h1{
    text-align: center;
    font-size: 40px;
    color: #242524ff;
    font-weight: 700;

}

.Aboutteam .cards{
    margin-top: 20px;
    padding: 10px 40px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
}

.Aboutteam .card{
    width: 295px;
    height: 335px;
    border-radius: 12px;
    border: 1px solid #ebebeaff;
    box-shadow: 0px 0px 1px #171a1f12, 0px 0px 2px #171a1f1f;
    padding: 20px;
}

.Aboutteam .card .fou{
    width: 120px;
    height: 120px;
    object-fit: cover;
    border-radius: 50%;
    overflow: hidden;
    border: 1px solid #242524ff;
    margin-left: 28%;
}

.Aboutteam .card h3{
    font-size: 18px;
    font-weight: 600;
    color: #242524ff;
    text-align: center;
    margin-top: 5px;
}

.Aboutteam .card small{
    font-size: 11px;
    color: #e8618cff;
    font-weight: 700;
    text-align: center;
    display: flex;
    justify-content: center;
}

.Aboutteam .card p{
    font-size: 11px;
    line-height: 20px;
    color: #8c8d8bff;
    font-weight: 400;
    margin-top: 20px;
    text-align: center;
}

@media (max-width: 950px) {
    .Aboutteam .cards{
         grid-template-columns: repeat(2, 1fr);
    }

    .Aboutteam h1{
         font-size: 1.2em;
    text-align: center;
    }

    .Aboutteam .card{
         width: 300px;
        height: 280px;
    }
}

@media (max-width: 600px) {
    .Aboutteam .cards {
        grid-template-columns: 1fr;
    }
}