.Auth{
    box-sizing: border-box;
    margin-top: 100px;
    padding: 20px;
}



.Auth h2{
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    color: #242524ff;
    font-weight: 700;
    line-height: 28px;
    margin-top: 30px;
    margin-bottom: 20px;
}

.Auth input{
    width: 400px;
    height: 40px;
    padding-left: 20px;
    padding-right: 12px;
    line-height: 22px;
    font-weight: 400;
    border-radius: 6px;
    border: 1px solid #ebebeaff;
    outline: none;
    font-size: 12px;
    margin-left: 35%;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 40px;
} 

 .Auth button{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 400px;
    height: 40px;
    color: #fff;
    background: #636ae8ff;
    font-weight: 500;
    line-height: 22px;
    border-radius: 6px;
    border: none;
    padding: 0 12px;
    margin-top: 10px;
    margin-left: 35%;
    cursor: pointer;
} 

 .Auth button:hover{
    background: #2c35e0ff;
} 

 .Auth p{
    font-size: 12px;
    color: #242524ff;
    font-weight: 400;
    line-height: 24px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
} 

 .Auth span{
    color: #e8618cff;
    margin-right: 5px; 
}

/* Media Queries for Responsiveness */
 @media (max-width: 1200px) {
    .Auth input,
    .Auth button {
        width: 90%; 
        margin-left: 5%; 
    } 

     .Auth img {
        width: 120px; 
    }
} 

 @media (max-width: 768px) {
    .Auth input,
    .Auth button {
        width: 95%; 
        margin-left: 2.5%; 
    } 

     .Auth img {
        width: 100px; 
    } 

     .Auth h2 {
        font-size: 18px; 
    } 

     .Auth p {
        font-size: 11px; 
    }
}
    


 @media (max-width: 480px) {
    .Auth input,
    .Auth button {
        width: 100%; 
        margin-left: 0; 
    }

    .Auth img {
        width: 80px; 
    }

    .Auth h2 {
        font-size: 16px; 
    }

    .Auth p {
        font-size: 10px; 
    }
} 
